import axios from 'axios'

const state = {
    data:[],
    hello:"Fuck You",
    load:false,
  };
  
  const mutations = {
    setData: (state,data) => (
        state.data = data
    ),
    setLoad: (state,data) => (
      state.load = data
    )
    
  }
  
  const actions = {
    async fetchData({commit}){
       console.log("Trying to Fetch");
        const response = await axios.get("https://toronto-pike-default-rtdb.firebaseio.com/PikeRecord.json");
         commit("setData", response.data)
         
      },
      quickFetch({commit}){
        axios.get("https://toronto-pike-default-rtdb.firebaseio.com/PikeRecord.json")
                  .then(res => { 
                       commit("setData", res.data)
                   })
                   .catch(error => console.log(error))
        console.log("Quick Fetch");   
   
      },

       loadPage({commit}){
         if(state.data.length!=0)
         commit("setLoad", true)
         else
          commit("setLoad",false)
      }
  };
  
  const getters = {
    dataList:state => {
      return state.data;
     },
     helloGet:state => {
      return state.hello;
     },
     getload:state =>{
       return state.load;
     }
  };
  
  export default {
    state,
    mutations,
    actions,
    getters
  };