import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store/index.js'



Vue.use(VueRouter)

var load=store.getters['getload'];

function guardMyroute(to, from, next)
{   
   var data=store.getters['dataList'];
    if(data.length<1)
       next("/loading");
     else
       next();  
   
 }

const routes = [
  {
    path: '/',
    name: 'Home',
    beforeEnter:guardMyroute,
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Home.vue')
    }

  },
  {
    path: '/Records',
    name: 'Records',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Records.vue')
    }
  },
  {
    path: '/Admin',
    name: 'Admin',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Admin.vue')
    }
  },
  ,
  {
    path: '/Loading',
    name: 'Loading',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Loading.vue')
    }
  }


]

const router = new VueRouter({
  routes
})
